.review__switch {
  display:flex;
  align-items: center;
  justify-content: center;

}
.interactive-avatar {
  width: rem(74px);
  height: rem(74px);
  border-radius: 50%;
  overflow: hidden;
  margin-right: rem(6px);
  border: 6px solid transparent;

  &:hover {
    border-color: #365A49;
  }


  &:last-child {
    margin-right: 0;
}}

.active{
  border-color: #365A49;
}

.interactive-avatar__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}